import { login, register} from '../../api/auth'
import { mapMutations } from 'vuex'
export default {
    name: 'AuthIndex',
    middleware: 'nonauth',
    computed: {
        isLogin() {
            return this.$route.name === 'login'
        }
    },
    data() {
      return {
        isLoading: false,
        userData: {
          username: "",
          email: "",
          password: ""
        },
        errorsArr: []
      }
    },
    methods: {
      async handleSubmit() {
        this.isLoading = true
        if(this.isLogin) {
          let [err, res] = await login({
            user: this.userData
          })
          this.handleAuth(err, res)
          this.isLoading = false
        }else {
          let [err, res] = await register({
            user: this.userData
          })
          this.handleAuth(err, res)
          this.isLoading = false
        }
      },
      handleAuth(err, res) {
        this.errorsArr = []
        if(err) {
          let { errors } = err.response.data
          Object.keys(errors).forEach(err => {
            this.errorsArr.push({
              type: err,
              content: errors[err]
            })
          })
          console.log(errors);
        }else {
          console.log(res)
          this.setUser(res.user)
          this.$router.replace('/')
        }
      },
      ...mapMutations({
        setUser: 'setUser'
      })
    }
}
